import React, { Component } from "react";
import ProjectInfoBox from 'src/components/ProjectInfoBox';

import { Link } from "gatsby"
import styled from 'styled-components';

import MEDIA_QUERIES from 'src/consts/media-queries';
import VARIABLES from 'src/consts/variables';

import { FaEdit, FaRegCommentDots } from 'react-icons/fa';
import CaretRightActive from '../images/caret-right-active.svg';

const TableOfContentsWrapper = styled.aside`
  ${MEDIA_QUERIES.LAPTOP_MIN} {
    position: absolute;
    right: 0;
    bottom: 0;
  }
`

const TableOfContentsContainer = styled.div`

  font-size: 12px;
  color: #737086;
  overflow: auto;

  .active {
    color: #3892e3;  
    background: url(${CaretRightActive}) no-repeat;
    background-position: 0px 4px;
  }

  .menu-caret {
    width: ${VARIABLES.WIDTH_TABLE_OF_CONTENTS_DESKTOP}px;

    ${MEDIA_QUERIES.LAPTOP_MIN} {
      width: ${VARIABLES.WIDTH_TABLE_OF_CONTENTS_LAPTOP}px;
    }

    ${MEDIA_QUERIES.TABLET} {
      max-width: 80vw;
    }

    &:hover {
      background: url(${CaretRightActive}) no-repeat;
      background-position: 0px 4px;
    } 
  }

  ${MEDIA_QUERIES.LAPTOP_MIN} {
    position: fixed;
    top: ${VARIABLES.HEIGHT_HEADER_DESKTOP}px;
    padding: 30px 0 100px 0;
    width: ${VARIABLES.WIDTH_TABLE_OF_CONTENTS_LAPTOP}px;
    max-height: calc(100vh - ${VARIABLES.HEIGHT_HEADER_DESKTOP}px);
    &.overlapping {
      position: absolute;
      bottom: 0;
      top: auto;
    }
  }
  
  ${MEDIA_QUERIES.DESKTOP_MIN} {
    width: ${VARIABLES.WIDTH_TABLE_OF_CONTENTS_DESKTOP}px;
    margin-left: 82px;
  }

  ${MEDIA_QUERIES.HD_ONLY} {
    margin-left: 65px;
  }

  ${MEDIA_QUERIES.TABLET} {
    display: none;
  }

  .container-label {
    color: #111a44;
    opacity: 0.5;
    font-size: 12px;
    line-height: 15px;
    padding-left: 9px;
    margin-bottom: 13px;
  }

  .link-label {
    color: #111a44;
    margin-bottom: 7px;
    margin-left: -2px;
    position: relative;
  }

  .text-middle-align {
    display: inline-block;
    vertical-align: middle;
  }

  .page-title {
    color: #111a44;
    font-weight: 600;
    line-height: 22px;
    font-size: 14px !important;
    display: block;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    border-bottom: 1px solid #c5c5c5;
    margin-bottom: 6px;

    ${MEDIA_QUERIES.TABLET} {
      display: none;
    }
  }

  ul {
    font-size: 12px; 
    color: #111a44;
    > li {
      padding-top: 2px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      &:last-of-type{
        margin-bottom: 6px;
      }
      > ul{
        padding-left: 10px;
      }
    }
  }

  a {
    display: block;
    padding: 2px 11px;
    border-radius: 3px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    &:hover{
      color: #3892e3;
    }
  }
`;

class TableOfContents extends Component {
  constructor(props) {
    super(props);
    this.state = {
      scrollingState: ""
    };
    this.handleScroll = this.handleScroll.bind(this)
  }

  componentDidMount(){
    window.addEventListener('scroll', this.handleScroll);
    window.addEventListener('resize', this.handleScroll);
    setTimeout(()=>{this.handleScroll()}, 500);
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScroll);
    window.removeEventListener('resize', this.handleScroll);
  }

  /**
   * Handling how the menu will stay in the screen when the menu having different height on desktop
   */
  handleScroll() {

    let headerHeight = (document.querySelector('.Header') || {}).offsetHeight || 0;
    let currentHeight = (this.wrapperElem || {}).offsetHeight || 0;

    //Determine if the top edge of the footer is overlapping the bottom edge of menu.
    let overlapping = document.querySelector('.Footer').getBoundingClientRect().top - headerHeight - currentHeight < 0;

    this.setState({overlapping});
  }


  render() {
    let { 
      htmlContent, contentTitle, slug, isTagged, currentVersion, orgName, repoName,
      frontMatter: { edit_link, request_changes_link } 
    } = this.props;
    // Only show the edit or request changes link if not null under two cases:
    // 1. If the repo is untagged
    // 2. If the repo current version is "latest"
    const shouldDisplay = ((isTagged && currentVersion === "latest") || !isTagged);

    return (
      <TableOfContentsWrapper>
        <TableOfContentsContainer ref={e => this.wrapperElem = e}
                              className={"TableOfContents" +
                                          (this.state.overlapping ? " overlapping" : '')}>
          {
            (edit_link && shouldDisplay) &&
            <div className="link-label">
               <a href={edit_link} target="_blank" rel="noopener noreferrer">
                <FaEdit className="text-middle-align" /> <span className="text-middle-align">
                  Edit
                </span>
               </a>
            </div>
          }
          {
            (request_changes_link && shouldDisplay) &&
            <div className="link-label">
               <a href={request_changes_link} target="_blank" rel="noopener noreferrer">
                <FaRegCommentDots className="text-middle-align" /> <span className="text-middle-align">
                  Request Changes
                </span>
               </a>
            </div>
          }

          <ProjectInfoBox
            orgName={orgName}
            repoName={repoName}
            isTagged={isTagged}
            currentVersion={currentVersion}
            />
          
          <div className="container-label">
            On this page:
          </div>

          { contentTitle &&
            <div className="page-title">
              <Link id="ToCHeader" to={slug}>{contentTitle}</Link>
            </div>
          }

          {/* For ContentPage from markdown, htmlContent will be passed in props for rendering the markdown content. */}
          {/* Otherwise, a customized componentContent for RpcPage will be passed in props for rendering . */}
          { htmlContent
            ? <div dangerouslySetInnerHTML={{ __html: htmlContent }}/>
            : this.props.componentContent
          }
        </TableOfContentsContainer>
      </TableOfContentsWrapper>
    )
  }

}

export default TableOfContents;
