import "./ContentPage.scss";

import React from "react";
import { graphql } from "gatsby"
import { Location } from "@reach/router";

import FullTemplate from 'src/templates/FullTemplate';
import GtmDataLayer from 'src/components/GtmDataLayer';
import TableOfContents from './components/TableOfContents';
import Breadcrumbs from './components/Breadcrumbs';

import { PageTitle } from './styled-components/document-page-styled-components';

class ContentPage extends React.Component {
  render(){
    const {
      site: {
        pathPrefix
      },
      allMarkdownRemark: { nodes }, 
      markdownRemark: {
        tableOfContents,
        html,
        frontmatter,
        fields: {
          articleTitle,
          contentTitle,
          headTagTitle,
          isCollection,
          isTagged,
          organization: orgName,
          originalKey: pathKey,
          pathData,
          repository: repoName,
          showSearch,
          slug,
          tagsList,
          latestDDVersion,
          importedSummary,
          hasPrimaryNavigation,
          primaryNavData,
          currentVersion,
          currentDDVersion,
          summaryJson,
        }
      }
    } = this.props.data;
    let projectInfoVersion = null;
    const hidesVersion = null;
    const pathInfo = JSON.parse(pathData);
    const summary = JSON.parse(summaryJson);
    const parsedTagsList = JSON.parse(tagsList);
    const mainNavData = (primaryNavData) ? JSON.parse(primaryNavData) : [{"text":"NONE","path":"EMPTY"}];
    const originalVersionObject = (isTagged) ? 
      parsedTagsList.filter(tag => currentDDVersion === tag.tag_reduced)[0] : null;

    if (originalVersionObject) {
      projectInfoVersion = originalVersionObject.tag;
      if (projectInfoVersion[0] !== 'v') {
        projectInfoVersion = "v"+projectInfoVersion;
      }
    }

    return (
      <FullTemplate
        orgName={orgName}
        repoName={repoName}
        contentTitle={contentTitle}
        currentVersion={currentVersion}
        hidesVersion={hidesVersion}
        showSearch={showSearch}
        isTagged={isTagged}
        title={headTagTitle}
        summary={summary}
        pathData={pathInfo}
        slugs={nodes}
        currentDDVersion={currentDDVersion}
        latestDDVersion={latestDDVersion}
        isCollection={isCollection}
        hasPrimaryNavigation={hasPrimaryNavigation}
        primaryNavData={mainNavData}
        tagsList={tagsList}
        importedSummary={importedSummary}
        pathKey={pathKey}
        pathPrefix={pathPrefix}
      >
        <GtmDataLayer
          orgName={orgName}
          repoName={repoName}
          currentVersion={currentVersion}
          hidesVersion={hidesVersion}
          />
        <article className="ContentPage document-page-container">
          <TableOfContents
            isTagged={isTagged}
            currentVersion={projectInfoVersion}
            frontMatter={frontmatter}
            orgName={orgName}
            repoName={repoName}
            htmlContent={tableOfContents}
            contentTitle={contentTitle}
            slug={slug}
          />
        <Location>
        {
          ({ location }) => (
              <section>
                <Breadcrumbs location={location} 
                  slug={slug}
                  title={contentTitle}
                  pathKey={pathKey}
                  pathData={pathInfo}
                  currentVersion={currentVersion}
                  isTagged={isTagged}
                  repoName={repoName}
                  importedSummary={importedSummary}
                  isCollection={isCollection}
                  />
                { articleTitle !== "NULL" &&
                  <PageTitle title={contentTitle} className="document-page-title">
                    <h1>{contentTitle}</h1>
                  </PageTitle>
                }
                <div className="document-page-content"
                  dangerouslySetInnerHTML={{ __html: html }}
                />
              </section>
          )
        }
        </Location>
        </article>
      </FullTemplate>
    )
  }
}

export default ContentPage

export const query = graphql`
    query($slug: String!) {
        site {
          pathPrefix
        }
        allMarkdownRemark {
          nodes {
            fields {
              slug
            }
          }
        }
        markdownRemark(fields: { slug: { eq: $slug } }) {
          html
          tableOfContents
          frontmatter {
            edit_link
            request_changes_link
          }
          fields {
            articleTitle
            contentTitle
            headTagTitle
            internalSummary
            isCollection
            isRoot
            isTagged
            organization
            originalKey
            pathData
            repository
            showSearch
            slug
            tagsList
            latestDDVersion
            importedSummary
            hasPrimaryNavigation
            primaryNavData
            currentVersion
            currentDDVersion
            summaryJson
          }
        }
    }
`
